<template>
  <button
    class="up-button"
    :class="{ show: !atTop }"
    @click="scrollTop"
    :disabled="atTop"
  >
    <p>Go Top!</p>
  </button>
</template>

<script>
export default {
  name: "UpButton",
  data() {
    return {
      atTop: true,
    };
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      const { scrollY } = window;
      this.atTop = scrollY === 0 ? true : false;
    },
    scrollTop() {
      window.scroll({
        top: -scrollY,
        behavior: "smooth",
      });
    },
  },
};
</script>

<style scoped>
.up-button {
  position: fixed;
  bottom: -5rem;
  right: 2rem;
  border: none;
  border-radius: 8px;
  z-index: 999;
  background-color: var(--pure-white);
  border: 2px solid var(--the-blue);
  padding: 4px 8px;
  transition: bottom 0.3s linear;
}

.up-button:hover {
  cursor: pointer;
}

p {
  color: var(--the-blue);
  font-weight: 700;
}

.show {
  bottom: 2rem;
  transition: bottom 0.6s linear;
}
</style>