<template>
  <div id="navbar">
    <div class="brand">
      <div class="title">
        <h1
          :class="[{ 'show-title': showTitle }, { 'show-border': showBorder }]"
        >
          <img src="../assets/images/c.svg" alt="" />lément's
        </h1>
      </div>
    </div>
    <nav>
      <ul class="navlist">
        <li class="navItem" v-for="navItem of navlist" :key="navItem.id">
          <a :href="navItem.to">
            <p>
              {{ navItem.name }}
            </p>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      navlist: [
        {
          id: 1,
          name: "Profile",
          to: "#profile",
        },
        {
          id: 2,
          name: "Tech_Stack",
          to: "#profile",
        },
        {
          id: 3,
          name: "Portfolio",
          to: "#portfolio",
        },
        {
          id: 4,
          name: "Education",
          to: "#education",
        },
        {
          id: 5,
          name: "Contact",
          to: "#contact",
        },
      ],
      showTitle: false,
      showBorder: false,
    };
  },
  mounted() {
    setTimeout(() => {
      this.showTitle = true;
      setTimeout(() => {
        this.showBorder = true;
      }, 1200);
    }, 800);
  },
};
</script>

<style scoped>
#navbar {
  background-color: var(--pure-white);
  width: 100%;
  height: 172px;
}

.brand {
  height: 120px;
  padding-bottom: 12px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
}

.title {
  overflow: hidden;
}

.title img {
  transform: translateY(2px);
  width: 32px;
  height: 32px;
  margin-right: 2px;
}

.title h1 {
  font-size: 36px;
  position: relative;
  top: 200px;
  transition: top 0.5s linear;
}

.title h1.show-title {
  top: 0px;
  transition: top 1.3s linear;
}

.title h1::after {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  content: "";
  width: 0%;
  height: 3px;
  background-color: var(--the-blue);
  transition: width 0.3s linear;
}

.title h1.show-border::after {
  transition: width 0.3s 0.3s linear;
  width: 100%;
}

nav {
  height: 40px;
  position: sticky;
  top: 2px;
  z-index: 999;
  background-color: #fff;
  border-top: 2px solid var(--the-blue);
  border-bottom: 2px solid var(--the-blue);
  overflow-x: scroll;
  margin-bottom: 48px;
}

nav {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

nav::-webkit-scrollbar {
  display: none;
}

.navlist {
  padding-left: 12px;
  /* width: 100%; */
  height: 100%;
  display: flex;
  flex-direction: row;
}

.navItem {
  height: 100%;
  width: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  z-index: 1;
  position: relative;
  padding: 0 8px;
}

.navItem p {
  height: 20px;
  color: var(--dark-grey);
  color: var(--the-blue);
  font-weight: 700;
  line-height: 20px;
  text-align: center;
  position: relative;
}

.navItem p::after {
  position: absolute;
  left: 0;
  top: 0;
  content: "";
  width: 0px;
  height: 20px;
  border-bottom: 2px solid var(--light-grey);
  transition: width 0.3s 0 linear;
}

.navItem:hover p::after {
  width: 100%;
  transition: width 0.15s linear;
}

@media (min-width: 376px) {
  /* .navlist {
    align-items: center;
  } */
}

@media (min-width: 480px) {
  .navlist {
    padding-left: 0;
    justify-content: space-around;
  }
}
</style>