<template>
  <div
    :id="cardId"
    :ref="cardId"
    class="card"
    v-touch="clickHandler"
    v-touch:swipe="swipeHandler"
  >
    <div class="image">
      <img class="card-image" :src="cardData.img | emptyImageFilter" alt="" />
      <img class="card-gif" :src="cardData.gif | emptyImageFilter" alt="" />
      <div class="link">
        <a :href="cardData.github" target="_blank">
          <img :src="link" alt="" />
          <p>Github</p>
        </a>
      </div>
      <a :href="cardData.github" target="_blank">
        <p class="name">{{ cardData.title }}</p></a
      >
    </div>
    <div class="content">
      <p class="type">
        <i class="fas fa-dot-circle"></i>
        類型：{{ cardData.type }}
      </p>
      <p class="techs">
        <!-- <i class="fas fa-dot-circle"></i>
        技術： -->
        <!-- {{ cardData.techs | listFormatFilter }} -->
        <!-- {{ cardData.techs }} -->
        <span
          class="tech-tag"
          v-for="(tech, index) of cardData.techs"
          :key="index"
          >{{ tech }}</span
        >
      </p>
      <p class="spec">
        <i class="fas fa-dot-circle"></i>
        功能：
        <!-- {{ cardData.spec | listFormatFilter }} -->
        {{ cardData.spec | listFormatFilter }}
      </p>
      <p class="text">
        <i class="fas fa-dot-circle"></i>
        簡述：
        <br />
        {{ cardData.text }}
      </p>
    </div>
  </div>
</template>

<script>
import link from "../../assets/images/link.svg";
import { emptyImageFilter } from "../../utils/mixin";
export default {
  name: "PortfolioCard",
  mixins: [emptyImageFilter],
  props: {
    cardId: {
      type: String,
      required: true,
    },
    cardData: {
      type: Object,
      required: true,
    },
    cardClicked: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      link,
      // showCard: false,
    };
  },
  methods: {
    clickHandler() {
      const targetCard = this.$refs[this.cardId];
      this.$emit("afterCardClicked", targetCard);
    },
    swipeHandler(e) {
      console.log(e);
    },
  },
  filters: {
    listFormatFilter(list) {
      return list.join("、");
    },
  },
};
</script>

<style scoped>
.card {
  background-color: var(--pure-white);
  border-radius: 8px;
}

.image {
  width: 100%;
  height: 240px;
  overflow: hidden;
  position: relative;
}

.image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card:hover .card-image {
  position: absolute;
  opacity: 0;
}

.link {
  position: absolute;
  width: 32px;
  height: 24px;
  overflow: hidden;
  transition: width 0.2s linear;
  background-color: transparent;
  padding: 0px 4px;
  bottom: 12px;
  right: 12px;
}

.link a {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
}

.link a p {
  font-weight: 700;
}

.card .link a img {
  width: 100%;
  height: 100%;
  margin-right: 4px;
}

.card .link:hover {
  color: var(--font-white);
  background-color: var(--the-blue);
  transition: 0.3s linear;
  width: 60px;
}

.link:hover a p {
  color: var(--pure-white);
}

.link:hover img {
  display: none;
}

.name {
  position: absolute;
  left: 12px;
  bottom: 12px;
  background-color: var(--the-blue);
  color: var(--pure-white);
  padding: 0 8px;
  transition: 0.15s linear;
}

.name:hover {
  color: var(--the-blue);
  background-color: var(--pure-white);
  cursor: pointer;
  /* bottom: 16px; */
  transition: 0.15s linear;
}

.content {
  padding: 12px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.techs {
  margin: 4px 0px;
  width: 100%;
  /* padding: 4px; */
  display: flex;
  flex-flow: row wrap;
}

.tech-tag {
  padding: 0 8px;
  margin: 4px;
  border-radius: 8px;
  background-color: var(--light-grey);
  color: var(--pure-white);
  /* height: 30px; */
}

.tech-tag:not(:last-child) {
  margin-right: 4px;
}

.text {
  width: 100%;
  margin-top: 8px;
  /* overflow-y: scroll; */
}

@media screen and (min-width: 376px) {
}

@media screen and (min-width: 1030px) {
}
</style>